<template>
    <div class="wx-field" :style="fieldStyles" @click="clickHandler">
        <text :class="[labelPosition=='top'?'wx-text-top':'wx-text']" :style="textTitleStyles">{{ label }}</text>
        <div class="wx-content">
            <input
                    v-if="!disabled"
                    @input="handleChange"
                    @blur="blur"
                    class="wx-input"
                    :type="type"
                    :style="inputStyles"
                    :maxlength="maxlength"
                    :autofocus="autofocus"
                    :disabled="disabled"
                    :value="value"
                    :placeholder="placeholder"/>
            <text v-if="disabled" class="wx-cli-text" :style="cliTextStyles">{{value=='' ? placeholder : value}}</text>
            <text class="wx-unit" v-if="unit">{{unit}}</text>
            <text v-if="hasArrow" class="iconfont wx-enter">&#xe65d;</text>
        </div>
    </div>
</template>
<style scoped>
    .wx-field {
        width: 750px;
        height: 100px;
        flex-direction: row;
        align-items: center;
        border-bottom-width: 1px;
        border-bottom-style: solid;
        border-bottom-color: #DCDCDC;
        flex-wrap: wrap;
    }

    .wx-text {
        font-size: 34px;
        color: #333333;
        width: 180px;
        flex-wrap: nowrap;
    }

    .wx-input {
        font-size: 32px;
        color: #333333;
        height: 110px;
        line-height: 110px;
        flex: 3;
        text-align: left;
    }

    .wx-content {
        flex-direction: row;
        flex: 1;
    }

    .wx-cli-text {
        color: #999999;
        font-size: 32px;
        flex-wrap: nowrap;
        padding-top: 30px;
    }

    .wx-unit {
        font-size: 32px;
        width: 50px;
        /*margin-top: 10px;*/
    }

    .wx-enter {
        color: #7A818B;
        font-size: 32px;
        margin-top: 30px;
    }

    .right-arrow {
        width: 22px;
        height: 22px;
        margin-top: 20px;
        border-bottom-width: 2px;
        border-bottom-style: solid;
        border-bottom-color: #DCDCDC;
        border-right-width: 2px;
        border-right-style: solid;
        border-right-color: #DCDCDC;
        /*margin-right: 4px;*/
        transform: rotate(-45deg);
    }

    /*label在上边的情况*/
    .wx-text-top {
        width: 750px;
        padding-top: 40px;
        /*padding-bottom: 40px;*/
        font-size: 34px;
        color: #333333;
    }

    .iconfont {
        font-family: iconfont;
    }
</style>
<script>
    import mixins from '../utils/mixins'
    const modal = weex.requireModule('modal')
    export default {
        mixins:[mixins],
        props: {
            width: {
                type: String,
                default: '750px'
            },
            cliWidth: {
                type: String
            },
            titleWidth: {
                type: String
            },
            height: {
                type: String,
                default: '100px'
            },
            styles: {
                type: Object,
                default: function () {
                    return {}
                }
            },
            inputStyles: {
                type: Object
            },
            label: {
                type: String,
                default: ''
            },
            labelPosition: {
                type: String,
                default: 'left'
            },
            type: {
                type: String,
                default: 'text'
            },
            maxlength: {
                type: String,
                default: '200'
            },
            autofocus: {
                type: Boolean,
                default: false
            },
            disabled: {
                type: Boolean,
                default: false
            },
            placeholder: {
                type: String,
                default: ''
            },
            unit: {
                type: String
            },
            hasArrow: {
                type: Boolean,
                default: false
            },
            value: {
                type: String
            }
        },

        data () {
            return {
                fieldStyles: {}
            }
        },

        created () {
            this.setStyle()
        },
        watch: {
            'value': function () {
                if(this.value != ''){
                    this.cliTextStyles.color = '#333333'
                }else{
                    this.cliTextStyles.color = '#999999'
                }
            }
        },

        methods: {
            setStyle () {

                // fieldStyles 样式
                const baseCss = {
                    height: this.height,
                    width: this.width,
                }
                this.fieldStyles = Object.assign({},  baseCss, this.styles)

//                if(this.disabled){
//                    modal.toast({
//                        message: this.width.replace('px','') - 26 + 'px'
//                    })
//                }

                // cliTextStyles样式
                let width = ''
                if(this.cliWidth != null){
                    width = this.cliWidth
                }else{
                    width = this.width
                }
                const cliTextCss = {
                    width: width.replace('px','') - 26 + 'px',
                    color: this.value == '' ? '#999999' : '#333333'
                }
                this.cliTextStyles = Object.assign({},  cliTextCss)

                if(this.titleWidth != null ){
                    // textTitleStyles 样式
                    const titleStyles = {
                        width: this.titleWidth
                    }
                    this.textTitleStyles = Object.assign({},  titleStyles)
                }

            },

            handleChange (e) {
                this.$emit('input', e.value)
            },

            blur () {
                this.$emit('wxBlur', this.inputValue);
            },

            clickHandler(){
                if (this.disabled){
                    this.$emit('wxClick')
                }else {
                    return
                }
            }
        }
    }
</script>


